.header-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #f8f8f8;
  padding: 10px 20px;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.1);
}

nav ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
}

nav li {
  margin-right: 20px;
}

nav a {
  text-decoration: none;
  color: #333;
}

.round-button {
  display: inline-block;
  border-radius: 30%;
  background-color: #2C5AB4;
  color: white;
  padding: 10px;
  text-align: center;
  cursor: pointer;
  border: none;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.1);
}

.round-button:hover {
  background-color: #555;
}
/*content*/









/* footer */
.footer-con{
  text-align: center;
}
footer{
  border-top: 2px solid black;
  width: 80%;
  margin : auto !important;
}

.left-div, .right-div {
  display: inline-block;
}

.left-div {
  width: 60%;
  text-align: start ;
  color: #2C5AB4;
}

.right-div {
  width: 40%;
  text-align: end;
  
}

@media only screen and (max-width: 768px) {
  .left-div, .right-div {
    display: block;
    width: 100%;
  }
}